.app {
  font-family: Arial, sans-serif;
  margin: 20px;
  max-width: 600px;
}

.add-input, .add-button {
  margin: 10px 0;
}

.list-item {
  border-left: 1px solid #ddd;
  margin: 10px 0;
  padding-left: 10px;
}

.level-0 {
  border-color: #f00;
}

.level-1 {
  border-color: #0f0;
}

.level-2 {
  border-color: #00f;
}

/* Add more levels if needed */

.delete-button {
  margin-left: 10px;
  background-color: #f00;
  color: #fff;
  border: none;
  padding: 5px;
  cursor: pointer;
}

.add-button {
  background-color: #0f0;
  color: #fff;
  border: none;
  padding: 5px;
  cursor: pointer;
}

